import * as React from "react";
import * as PropTypes from "prop-types";
import { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

import Checkbox from "./Checkbox";

export interface SwitchProps extends NamespaceWrapperProps {
    /**
     * Name of the field
     */
    name ?: string;

    /**
     * Is it checked by default ?
     */
    defaultChecked ?: boolean;

    /**
     * Is it currently checked
     */
    checked ?: boolean;

    /**
     * Triggered on any change
     */
    onChange ?: (checked: boolean) => void;

    /**
     * Disables the switch
     */
    disabled ?: boolean;

    /**
     * Add a class name
     */
    className ?: string;
}

export default class Switch extends React.Component<SwitchProps, {}> {
    public static displayName = "Switch";

    public static propTypes = {//
        "name": PropTypes.string, //
        "defaultChecked": PropTypes.bool, //
        "checked": PropTypes.bool, //
        "onChange": PropTypes.func, //
        "disabled": PropTypes.bool, //
        "className": PropTypes.string //
    };

    public render() {
        return <Checkbox isSwitch {...this.props} />;
    }
}
